.container{
    width: 100vw;
    height: calc(100vh - 140px);

    padding-top: 150px;
    margin-top: 100px;
    display : flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    top:100px;
    left:0;
  
    
    
}

.details{
    display: flex;
    flex-direction: column;
    min-width: 100vw;
    padding-bottom: 25px;
    flex-wrap: wrap;
    position: absolute;
    top:70px;
    left:0px;
  
    padding-left:50px;
    


}

.devidetabs{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding:8px;
    width: 50%;
    min-width: 200px;
    align-items: start;
    flex-wrap: wrap;
    border-bottom: 1px solid #2e7d32;
    margin-top: 20px;
    
}

.details_more{
   
    padding:25px 0px;
    padding-left: 30px;
    width: 100%;
    min-width: 500px;
    display: flex;
    height: 700px;
    
  
}

.details_more>div{
    width:50%;
   
   
}

.details_more table{
    width: 60%;
    border-collapse: collapse;
    border: 1px solid #1b5e20;
}

.details_more table tr td{
    padding: 10px;
}

.suggestions{
    position: absolute;
    top: -40px;
   
    padding-bottom: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    width:calc(100% - 50px);
}

.suggestion_chip{
    border: 1px solid #2e7d32;
    padding: 10px 20px; 
    border-radius: 10px;
    color: #2e7d32;
    font-weight: 600;
    margin-left:60px;
    background-color: white;
    
}

.suggestion_chip:hover{
    background-color: #2e7d32;
    color: white;
    cursor: pointer;
}

.mealName{
    width: 100%;
    font-size: 30px;
    font-weight: bold;
    color: #2e7d32;
    margin-left:  0px;
    position: relative;
    margin-top: -40px;
}


.ins_heading{
    font-size: 30px;
    border-bottom: 2px solid #2e7d32;
}



